import React from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import CardBox from '../components/cardbox';
import Layout from '../components/layout';
import styles from './cv.module.scss';
import timelineContent, { ObjectType, TypeLabels } from '../../content/timeline';

const TimeLineItem = ({ title, text }) => (
  <div className={styles.timelineItem}>
    <div className={styles.titleWithDot}>
      <div className={styles.circle} />
      {title}
    </div>
    <div className={styles.inside}>
      {text}
    </div>
  </div>
);

const Filter = ({ values, onChange, onClickAll }) => (
  <div className={styles.filterPanel}>
    <div 
      className={styles.filterItem}
      onClick={e => onClickAll(
        !Object.values(values).every(v => v)
      )}
    >
      <input checked={Object.values(values).every(v => v)} type="checkbox" />
      {' '}
      Show All
    </div>
    {
      Object
        .keys(ObjectType)
        .map(type => (
          <div 
            key={type}
            className={styles.filterItem}
            onClick={e => onChange(type, !values[type])}
          >
            <input checked={values[type]} type="checkbox" />
            {' '}
            {TypeLabels[type]}
          </div>
        ))
    }
  </div>
);

class CVPage extends React.Component {
  constructor() {
    super();
    const keys = Object.keys(ObjectType);
    const obj = {};
    keys.forEach((key) => {
      obj[key] = true;
    });
    this.state = {
      active: obj,
    };
  }

  filterChangeHandler = (key, value) => {
    this.setState(state => ({
      active: {
        ...state.active,
        [key]: value,
      },
    }));
  }

  fiterItem = (item) => {
    const { active } = this.state;
    if (item.type === undefined) {
      return true;
    }
    if (item.type instanceof Array) { // it is array
      // if some of this presented types is active then show it
      return item.type.some(t => active[t]);
    }
    return active[item.type];
  }

  clickAllHandler = (value) => {
    this.setState((state) => {
      const obj = { ...state.active };

      Object.keys(state.active).forEach((key) => {
        obj[key] = value;
      });

      return {
        active: obj,
      };
    });
  }

  render() {
    return (
      <Layout>
        <CardBox>
          <div style={{ paddingTop: 20 }}>
            <Filter
              values={this.state.active}
              onChange={this.filterChangeHandler}
              onClickAll={this.clickAllHandler}
            />
            <div className={styles.marginBottom}>
              <div className="text-center"><h3>Martin Schn√ºrer</h3></div>
            </div>
            <div className={styles.timeline}>
              <TransitionGroup>
                {
                  timelineContent.content
                    .filter(this.fiterItem)
                    .map(item => (
                      <CSSTransition
                        timeout={500}
                        key={item.title}
                        classNames={{
                          enter: styles.itemEnter,
                          enterActive: styles.itemEnterActive,
                          exit: styles.itemExit,
                          exitActive: styles.itemExitActive,
                        }}
                      >
                        <TimeLineItem text={item.text} title={item.title} />
                      </CSSTransition>
                    ))
                  }
              </TransitionGroup>
            </div>
          </div>
        </CardBox>
      </Layout>
    );
  }
}

export default CVPage;
